import React from "react";
import { HashLink as Link } from "react-router-hash-link";
import "./css/animate.css";

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.onClickDropdown = this.onClickDropdown.bind(this);
    this.state = {
      dropdownVisibility: {}
    };
  }

  onClickDropdown() {
    this.setState({ dropdownVisibility: { display: "none" } }); //hide the dropdown menu - landscape menu

    setTimeout(() => {
      this.setState({ dropdownVisibility: {} }); //remove any styling being applied now the menu is hidden
    }, 800);
  }

  render() {
    return (
      <div>
        <header>
          <div id="top" />

          <div className="top-header-area text-white row animated fadeInDown delay-0s">
            <span className="col-7 headerText">
              {" "}
              93 Irish Quarter South, Carrickfergus, BT38 8BW 17
            </span>{" "}
            <span className="col-5">
              <div className="alignRight">
                <a href="tel:+442893 367007">
                  <img
                    src={require("./img/phone.svg")}
                    className="phoneimg inlineBlock"
                    alt=""
                  />
                  <div className="headerText inlineBlock phonenumberWhite">
                    +44(0)2893 367007
                  </div>
                </a>
              </div>
            </span>
          </div>

          <div className="top-header-area-Bottom header2 animated fadeInUp delay-0s">
            <div className="center-image">
             Bed And Breakfast in Carrickfergus
            </div>
          </div>

          <div className="center-image">
            <div className="Aligner animated fadeIn">
              <div className="Aligner-item Aligner-item--fixed img-logo mainlogo">
                <a href="https://www.booking.com/hotel/gb/the-keep-guesthouse.en-gb.html">
                  <img
                    src={require("./img/logo-keep.png")}
                    className="logo"
                    alt=""
                  />
                </a>
              </div>
            </div>
          </div>

          <div className="titlepadding" />


          <div className="center-image landscapemenu">
            <span className="links">
              <ul
                className="noPaddingStart"
                data-animation="pulse"
                data-delay="5000ms"
              >
                <span className="menu-element wrapper">
                  <li className="menulinks">
                    <Link smooth to="/#about" className="effect-underline">
                      Welcome
                    </Link>
                  </li>
                </span>
                <span className="menuLinkSpacer" />
                <span className="menu-element wrapper">
                  <li className="menulinks">
                    <Link smooth to="/#accommodation" className="effect-underline">
                      Accommodation
                    </Link>
                  </li>
                </span>
                <span className="menuLinkSpacer" />

                <span className="menu-element wrapper">
                  <li className="menulinks">
                    <Link smooth to="/#attractions" className="effect-underline">
                      Attractions
                    </Link>
                  </li>
                </span>
                <span className="menuLinkSpacer" />

		<span className="menu-element wrapper">
                  <li className="menulinks">
                    <Link smooth to="/#booking" className="effect-underline">
                      Booking
                    </Link>
                  </li>
                </span>
                <span className="menuLinkSpacer" />

                <span className="menu-element wrapper">
                  <li className="menulinks">
                    <Link smooth to="/#reviews" className="effect-underline">
                      Reviews
                    </Link>
                  </li>
                </span>
                <span className="menuLinkSpacer" />
                <span className="menu-element wrapper">
                  <li className="menulinks">
                    <Link smooth to="/#gallery" className="effect-underline">
                      Gallery
                    </Link>
                  </li>
                </span>
                <span className="menuLinkSpacer" />
                <span className="menu-element wrapper">
                  <li className="menulinks">
                    <Link smooth to="/#contact" className="effect-underline">
                      Contact
                    </Link>
                  </li>
                </span>
              </ul>
            </span>
          </div>
        </header>

        <div className="titlepadding" />

        <nav className="navbar navbar-light bg-light mobilemenu">
          <Link className="navbar-brand" to="#" role="button" />
          <button
            className="navbar-toggler collapsed"
            type="button"
            data-toggle="collapse"
            data-target="#navbars"
            aria-controls="navbars"
            aria-expanded="false"
            aria-label="Toggle navigation"
            id="hamburger"
          >
            <span className="icon-bar top-bar" />{" "}
            <span className="icon-bar middle-bar" />{" "}
            <span className="icon-bar bottom-bar" />
          </button>

          <div className="collapse navbar-collapse" id="navbars">
            <ul className="navbar-nav center-image">
              <li className="menu-element nav-divider">
                <Link className="menulinks nav-link" smooth to="/#about">
                  Welcome
                </Link>
              </li>
              <li className="menu-element nav-divider">
                <Link className="menulinks nav-link" smooth to="/#accommodation">
                  Accommodation
                </Link>
              </li>
              <li className="menu-element nav-divider">
                <Link className="menulinks nav-link" smooth to="/#attractions">
                  Attractions
                </Link>
              </li>
	      <li className="menu-element nav-divider">
                <Link className="menulinks nav-link" smooth to="/#booking">
                  Booking
                </Link>
              </li>
              <li className="menu-element nav-divider">
                <Link className="menulinks nav-link" smooth to="/#reviews">
                  Reviews
                </Link>
              </li>
              <li className="menu-element nav-divider">
                <Link className="menulinks nav-link" smooth to="/#gallery">
                  Gallery
                </Link>
              </li>
              <li className="menu-element">
                <Link className="menulinks nav-link" smooth to="/#contact">
                  Contact
                </Link>
              </li>
            </ul>
          </div>
        </nav>

        <div className="mobilepadding" />
      </div>
    );
  }
}

export default Header;
