import React from "react";
import { HashLink as Link } from "react-router-hash-link";
import ScrollAnimation from "./react-animate-on-scroll_/scroll-animation.js";

class Footer extends React.Component {
  constructor(state, props) {
    super(state, props);

    this.state = {
      show: false, //show the scroll up button
      clicked: false //button has been clicked
    }; 

    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }

  componentDidUpdate(prevProps) {
    //show or hide the scrollToTop button based on the lightboxOpen status flag
    if (this.props.lightboxOpen !== prevProps.lightboxOpen) {	//prevProps.lightboxOpen
      //this.setState({ lightboxOpen: this.props.lightboxOpen });

      if (this.props.lightboxOpen === false) {
        this.setState({ show: true });
      }
      if (this.props.lightboxOpen === true) {
        this.setState({ show: false });
      }
    }
  }

  //Show or hide the scrollToTop component, based on current scroll position
  handleScroll() {
    if (window.pageYOffset > 400) {
      if (!this.state.show) {
        this.setState({ show: true });
      }
    } else {
      if (this.state.show) {
        this.setState({ show: false });

        if (this.state.clicked) {
          //if scroll up button has been clicked, then clear #top from the hash
          var noHashURL = window.location.href.replace(/#.*$/, "");
          window.history.replaceState("", document.title, noHashURL);

          this.setState({ clicked: false });
        }
      }
    }
  }

  scrollToTopClicked() {
    this.setState({ clicked: true });
  }

  render() {
    return (
      <div>
        <footer id="footer">
          <div className="row justify-content-center copyText">
            <div className="col-sm-5">
              <div className="center-image">
                <ScrollAnimation
                  animateIn="fadeInLeft"
                  delay={0}
                  offset={5}
                  animateOnce={true}
                >
                  Copyright © 2019 The Keep 
                </ScrollAnimation>
              </div>
            </div>

            <br />

            <div className="col-sm-5">
              <div className="center-image">
                <ScrollAnimation
                  animateIn="fadeInRight"
                  delay={0}
                  offset={5}
                  animateOnce={true}
                >
                  <a
                    href="mailto:libertyj29@gmail.com"
                    className="blackText blackTextH"
                  >
                    Developed by{" "}
                    <span className="designText">ahaha Design</span>
                  </a>
                </ScrollAnimation>
              </div>
            </div>
          </div>
        </footer>

        <Link
          smooth
          to="#top"
          id="scrollUp"
          className="scrolluplink"
          onClick={() => {
            this.scrollToTopClicked();
          }}
          style={{ visibility: this.state.show ? "visible" : "hidden" }}
        >
          <div className="scrollUpStyle" />
        </Link>
      </div>
    );
  }
}

export default Footer;
