import React from "react";
import ScrollAnimation from "./react-animate-on-scroll_/scroll-animation.js";
import Gallery from "./react-photo-gallery/Gallery.js";
import Lightbox from "./react-images-lightbox/Lightbox.js";
//import Lightbox from 'react-images';

//import LightboxB from 'react-image-lightbox';
//import 'react-image-lightbox/style.css'; // This only needs to be imported once in your app


const photos = [
  {
    src: require("./img/1.jpg"),
    caption: "The Keep",
    filter: "styling",
    width: 1,
    height: 1
  },
  {
    src: require("./img/3.jpg"),
    caption: "Family Room",
    filter: "colour",
    width: 1,
    height: 1
  },
  {
    src: require("./img/6.jpg"),
    caption: "Family Room",
    filter: "colour",
    width: 1,
    height: 1
  },
  {
    src: require("./img/4.jpg"),
    caption: "Family Room",
    filter: "colour",
    width: 1,
    height: 1
  },
  {
    src: require("./img/5.jpg"),
    caption: "Deluxe Double",
    filter: "extensions",
    width: 1,
    height: 1
  },
  {
    src: require("./img/7.jpg"),
    caption: "Deluxe Double",
    filter: "extensions",
    width: 1,
    height: 1
  },
  {
    src: require("./img/8.jpg"),
    caption: "Family Room",
    filter: "colour",
    width: 1,
    height: 1
  },
  {
    src: require("./img/2.jpg"),
    caption: "Breakfast",
    filter: "styling",
    width: 1,
    height: 1
  }
];

class MyGallery extends React.Component {
  constructor(state, props) {
    super();
    this.state = {
      currentImage: 0,
      currentFilter: "all",
      filteredPhotos: photos,
      width_: 0,
      height_: 0			//window.innerHeight
    };
    this.closeLightbox = this.closeLightbox.bind(this);
    this.openLightbox = this.openLightbox.bind(this);
    this.gotoNext = this.gotoNext.bind(this);
    this.gotoPrevious = this.gotoPrevious.bind(this);
this.handleHeight = this.handleHeight.bind(this);
  }

//  componentDidUpdate(prevProps) {
//console.log("WOWA "+window.innerHeight);
//};

  componentDidMount(state) {
let hei = window.innerHeight;
//this.setState({ height_: hei });
    window.addEventListener("scroll", this.handleHeight);
  }

  //Show or hide the scrollToTop component, based on current scroll position
  handleHeight(state) {

let hei = window.innerHeight;
let wei = window.innerWidth;
console.log(window.innerHeight);
//if((hei !== this.state.height_) && (hei !== undefined)){
//        this.setState({ height_: hei, width_: wei });
//}
console.log("HEIGHT!!");

  }



  //pass the new lightbox value back to parent, so that the scrollToTop button can be hidden if necessary
  onChangeLightboxStatus = value => {
    this.props.onChangeLightboxStatus(value);
  };

  openLightbox(event, obj) {
console.log(window.innerHeight);
    this.setState({
      currentImage: obj.index,
      lightboxIsOpen: true,
      height_: window.innerHeight
    });
    this.onChangeLightboxStatus(true);
  }
  closeLightbox() {
    this.setState({
      currentImage: 0,
      lightboxIsOpen: false
    });
    this.onChangeLightboxStatus(false);
  }
  gotoPrevious() {
    this.setState({
      currentImage: this.state.currentImage - 1
    });
  }
  gotoNext() {
    this.setState({
      currentImage: this.state.currentImage + 1
    });
  }

  //set the gallery filter value
  setGalleryFilter(value) {
    this.setState({
      currentFilter: value
    });

    //filter the photo gallery
    this.filterPhotos(value);
  }

  //set current filter link to active
  isActive(value) {
    return value === this.state.currentFilter ? "active" : "default";
  }

  //filter the photo gallery with the filter value
  filterPhotos = value => {
    let searchValue = value;
    let filteredPhotos = photos;

    if (searchValue !== "all") {
      filteredPhotos = filteredPhotos.filter(photo => {
        let photoCategory = photo.filter;
        return photoCategory.indexOf(searchValue) !== -1;
      });
      this.setState({
        filteredPhotos: []
      });

      setTimeout(() => {
        this.setState({ filteredPhotos }); //remove all photos, then after timeout show the filtered ones to apply the animation
      }, 5);
    } else {
      this.setState({
        filteredPhotos: []
      });
      setTimeout(() => {
        this.setState({ filteredPhotos: photos }); //remove all photos, then after timeout show the filtered ones to apply the animation
      }, 5);
    }
  };

  render() {
    return (
      <section id="gallery">
        <div className="center-image">
          <br />
          <ScrollAnimation
            animateIn="fadeInDown"
            animateOnce={true}
            offset={30}
          >
            <h2 className="titleText">GALLERY</h2>
          </ScrollAnimation>
        </div>


          <div className="col-sm-12 animated fadeInUp galleryFilterContainer">
            <ul id="filter">
              <li className={this.isActive("all")}>
                <div onClick={() => this.setGalleryFilter("all")}>All</div>
              </li>
              <li className={this.isActive("colour")}>
                <div onClick={() => this.setGalleryFilter("colour")}>
                  Family Room
                </div>
              </li>
              <li className={this.isActive("extensions")}>
                <div onClick={() => this.setGalleryFilter("extensions")}>
                  Deluxe Double
                </div>
              </li>
              <li className={this.isActive("styling")}>
                <div onClick={() => this.setGalleryFilter("styling")}>
                  Breakfast
                </div>
              </li>
            </ul>
          </div>

        <div className="gallery-container">
          <Gallery
            photos={this.state.filteredPhotos}
            margin={10}
            onClick={this.openLightbox}
          />
          <Lightbox
	    backdropClosesModal={true}
            images={this.state.filteredPhotos}
            onClose={this.closeLightbox}
            onClickPrev={this.gotoPrevious}
            onClickNext={this.gotoNext}
            currentImage={this.state.currentImage}
            isOpen={this.state.lightboxIsOpen}
            //height_={this.state.height_} 
            //width_={this.state.width_}
          />
        </div>
      </section>
    );
  }
}

export default MyGallery;
