import React from "react";
import Fade from "./react-slideshow/components/slideshow/fade.js";
import { Animated } from "react-animated-css";
import ScrollAnimation from "./react-animate-on-scroll_/scroll-animation.js";

const properties = {
  duration: 8000,
  transitionDuration: 500,
  infinite: true,
  indicators: true,
  arrows: true
};

class Reviews extends React.Component {
  constructor(state, props) {
    super(state, props);

    this.state = {
      visibility1: { opacity: 0 },
      visibility2: { opacity: 0 },
      visibility3: { opacity: 0 },
      index: 0,
      visible1: false,
      visible2: false,
      visible3: false
    };
  }

  onChangeIndexReviewsExtensions = value => {
    if (value === 0) {
      this.setState({
        index: value,
        visibility1: { opacity: 1 },
        visible1: true,
        visibility2: { opacity: 0 },
        visible2: false,
        visibility3: { opacity: 0 },
        visible3: false
      });
    }
    if (value === 1) {
      this.setState({
        index: value,
        visibility1: { opacity: 0 },
        visible1: false,
        visibility2: { opacity: 1 },
        visible2: true,
        visibility3: { opacity: 0 },
        visible3: false
      });
    }
    if (value === 2) {
      this.setState({
        index: value,
        visibility1: { opacity: 0 },
        visible1: false,
        visibility2: { opacity: 0 },
        visible2: false,
        visibility3: { opacity: 1 },
        visible3: true
      });
    }
  };

  render() {
    return (
      <section id="reviews" className="reviewBackground">
        <div className="center-image whiteText">
          <br />
          <ScrollAnimation
            animateIn="fadeInDown"
            animateOnce={true}
            offset={30}
          >
            <h2 className="titleText">REVIEWS</h2>
          </ScrollAnimation>
        </div>
        <br />

        <Fade
          {...properties}
          onChangeIndex={this.onChangeIndexReviewsExtensions}
        >
          <div className="each-slide">
            <div className="row justify-content-center">
              <div className="col-8 testimonialMobileWidth">
                <div className="row">
                  <div
                    className="col-sm-12 whiteText"
                    style={this.state.visibility1}
                  >
		    <Animated
                      animationIn="zoomIn"
                      animationInDelay={0}
                      isVisible={this.state.visible1}
                    >
                      Irish hospitality at its best<br /><i className="reviewName">Ray Harrison (November 2018)</i><br /><br />
                    </Animated>
                    <Animated
                      animationIn="fadeIn"
                      animationInDelay={150}e
                      isVisible={this.state.visible1}
                    >
                      What a gem this guest house is! Perfectly hosted by Heather and Raymond, who take customer service to a new level. Nothing is too much trouble. A real home from home look and feel. Excellent breakfast and our room was spacious,clean and comfortable. The location is absolutely perfect as a base for exploring the attractions of Carrickfergus and the North Antrim ciustline. Lots of local information available and Raymond is always at hand to offer any further assistance / local knowledge.<br />
Neat the harbour,marina and castle and local shops,bars and restaurants.<br />We will definitely stay there again when in Carrickfergus.
                    </Animated>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="each-slide">
            <div className="row justify-content-center">
              <div className="col-8 testimonialMobileWidth">
                <div className="row">
                  <div
                    className="col-sm-12 whiteText"
                    style={this.state.visibility2}
                  >
		    <Animated
                      animationIn="zoomIn"
                      animationInDelay={0}
                      isVisible={this.state.visible2}
                    >
                      Great start to our Ireland Vacation<br /><i className="reviewName">Katherine B (July 2018)</i><br /><br />
                    </Animated>
                    <Animated
                      animationIn="fadeIn"
                      animationInDelay={150}
                      isVisible={this.state.visible2}
                    >
                      We (group of four friends) stayed in the family room upstairs. Great views to the waterfront, four comfortable beds, lots of space.<br />
Heather was very helpful and offered great restaurant suggests and also called our taxi driver for a tour in Belfast the next day. <br />
The location is great if you’re looking to walk to the castle/local parks. Parking is very convenient at the Keep (even for those of us used to driving on the right side of the road). <br />
Breakfast was amazing. Quintessential Irish Fare! <br />
Highly recommend!
                      <span role="img" aria-label="emoji">
                        ❤️
                      </span>
                    </Animated>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="each-slide">
            <div className="row justify-content-center">
              <div className="col-8 testimonialMobileWidth">
                <div className="row">
                  <div
                    className="col-sm-12 whiteText"
                    style={this.state.visibility3}
                  >
		    <Animated
                      animationIn="zoomIn"
                      animationInDelay={0}
                      isVisible={this.state.visible3}
                    >
                      A great place with lovely hosts!<br /><i className="reviewName">Dawn S (August 2017)</i><br /><br />
                    </Animated>
                    <Animated
                      animationIn="fadeIn"
                      animationInDelay={150}
                      isVisible={this.state.visible3}
                    >
                      We loved staying at The Keep! It sits very near The Carrickfergus castle (a very quick walk), several pubs, a coffee shop, and restaurants. The Keep itself is very well-kept with lovely and very kind hosts. The breakfast was tasty and left us very full for a day of sight-seeing. Parking out front was easy. The rooms were nice-sized and quiet with solid wifi. <br />
We definitely recommend staying here should you find yourself in this lovely part of Northern Ireland!
                    </Animated>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Fade>
      </section>
    );
  }
}

export default Reviews;
