import React from "react";
import { withRouter } from "react-router-dom";
import { Route, Redirect, Switch } from "react-router-dom";
import Main from "./Main";

class AppRouter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      access: "none",
      lightboxOpen: false
    };
  }

  onChangeLightboxStatus = value => {
    this.setState({ lightboxOpen: value });
    this.props.onChangeLightboxStatus(value);
  };

  componentDidMount() {
    if (this.props.location.state) {
      //access from page needs scroll to top, access from menu does not change the scroll position
      this.setState({ access: this.props.location.state.access });
    }
  }

  render() {
    return (
      <Switch>
        <Route
          path="/"
          exact={true}
          render={props => (
            <Main
              {...props}
              Main
              onChangeLightboxStatus={this.onChangeLightboxStatus}
            />
          )}
        />
        <Redirect from="*" to="/" />
      </Switch>
    );
  }
}

export default withRouter(AppRouter);
