import React from "react";
import { withRouter } from "react-router-dom";
import ScrollAnimation from "./react-animate-on-scroll_/scroll-animation.js";

class Accommodation extends React.Component {
  render() {
    return (
      <div>
        <section id="accommodation">
          <div className="center-image">
            <ScrollAnimation
              animateIn="fadeInDown"
              animateOnce={true}
              offset={30}
            >
              <h2 className="titleText">ACCOMMODATION</h2>
            </ScrollAnimation>
          </div>
          <br />

          <div className="row">
            <div className="flexBoxStyle">
              <div className="col-sm-4">
                <ScrollAnimation animateIn="fadeInLeft" delay={0}>
                  <img
                    src={require("./img/room1.jpg")}
                    className="img-responsive"
                    alt=""
                  />
                </ScrollAnimation>
              </div>
              <div className="col-sm-6">
                <ScrollAnimation animateIn="fadeInRight" delay={0}>
                  <span className="center-image">
                    
                    All our rooms have been individually designed to the highest standard. Each contains original Art work specially commissioned to match in with the unique decor and character of the room.
		    <br />
                    <br />
                    Every room has its own remote control fan to keep you cool in the long hot summer nights as well as TV's for the nights when you just want to relax. It goes without saying all our rooms are en-suite with great power showers to add to your comfort.
                    <br />
                    <br />
                    Our <b>two extra large family rooms</b> can each hold up to four people with cot facilities or they can be used as a superior double or a roomier single room.
                  </span>
                </ScrollAnimation>
              </div>
            </div>
          </div>

          <br />
          <div className="stylistDivider" />

          <div className="row">
            <div className="flexBoxStyle">
              <div className="col-sm-4">
                <ScrollAnimation animateIn="fadeInLeft" delay={0}>
                  <img
                    src={require("./img/room4.jpg")}
                    className="img-responsive"
                    alt=""
                  />
                </ScrollAnimation>
              </div>
              <div className="col-sm-6">
                <ScrollAnimation animateIn="fadeInRight" delay={0}>
                 
                  We have <b>two double rooms</b> which both reflect the very high standards of comfort and design.
                  <br />
                  <br />
                  You can leave your car behind at night as all the Restaurants and Bars are within walking distance. 
                  <br />
                  <br />
                  For those who are here on business we will fit in with your working requirements arranging breakfast times around your working day and doing everything we can to accommodate your busy schedule. 
                  <br />
                  <br />
                  We pride ourselves in the Cleanness and Quality of The Keep and offer comfort and style at a very competitive rate.
                  <br />
                  <br />
                </ScrollAnimation>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default withRouter(Accommodation);
