import React from "react";
import "./css/animate.css";
import ScrollAnimation from "./react-animate-on-scroll_/scroll-animation.js";

class Attractions extends React.Component {
  render() {
    return (
      <div>
        <br />

        <section id="attractions" className="bg-grey">
          <br />

          <div className="center-image">
            <ScrollAnimation
              animateIn="fadeInDown"
              animateOnce={true}
              offset={30}
            >
              <h2 className="titleText">ATTRACTIONS</h2>
            </ScrollAnimation>
            <br />
            <div id="detail2">
              <div className="row">
                <div className="col-lg-10 mx-auto">
                  <span className="close">×</span>
                  <div id="detail-slider2" className="owl-carousel owl-theme" />
                  <div className="text-center">
                    <div id="detail-title2" className="title" />
                  </div>
                  <div id="detail-content2" />
                </div>
              </div>
            </div>
            <div id="service" className="center-image">
              <div className="row AttractionsPaddingLeft AttractionsPaddingRight">

                <div className="services-item col-lg-4 col-md-6">
                  <ScrollAnimation animateIn="fadeInUp" delay={0}>
                    <div className="myServices transformTop">
                      <a
                        href="https://discovernorthernireland.com/Carrickfergus-Castle-Carrickfergus-P2814/"
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        <img
                          src={require("./img/castle2.jpg")}
                          alt=""
                          className="img-fluid"
                        />
                        <div className="myserviceoverlay">
                          <div className="inner">
                            <h3 className="h4 mywork-title2">Carrickfergus Castle</h3>
                            <p className="serviceViewMore">View more</p>
                          </div>
                        </div>
                      </a>
                    </div>
                  </ScrollAnimation>
                </div>

                <div className="services-item col-lg-4 col-md-6">
                  <ScrollAnimation animateIn="fadeInUp" delay={200}>
                    <div className="myServices">
                      <a 
                        href="https://discovernorthernireland.com/Giant-s-Causeway-National-Trust-Bushmills-P2800/"
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        <img
                          src={require("./img/causeway.jpg")}
                          alt=""
                          className="img-fluid"
                        />
                        <div className="myserviceoverlay">
                          <div className="inner">
                            <h3 className="h4 mywork-title2">Giants Causeway</h3>
                            <p className="serviceViewMore">View more</p>
                          </div>
                        </div>
                      </a>
                    </div>
                  </ScrollAnimation>
                </div>

                <div className="services-item col-lg-4 col-md-6">
                  <ScrollAnimation animateIn="fadeInUp" delay={400}>
                    <div className="myServices transformTop">
                      <a
			href="https://discovernorthernireland.com/The-Dark-Hedges-Stranocum-Ballymoney-P27502/"
			target="_blank"
                        rel="noopener noreferrer"
                      >
                        <img
                          src={require("./img/thrones.jpg")}
                          alt=""
                          className="img-fluid"
                        />
                        <div className="myserviceoverlay">
                          <div className="inner">
                            <h3 className="h4 mywork-title2">Game of Thrones</h3>
                            <p className="serviceViewMore">View more</p>
                          </div>
                        </div>
                      </a>
                    </div>
                  </ScrollAnimation>
                </div>
              </div>
            </div>
                <div className="attractionsContainer">
                  <ScrollAnimation animateIn="fadeInRight" delay={100} offset={80}>
                    Whether you are coming to stay with us for business, on a holiday or visiting the family there is plenty to keep you entertained.<br /><br />
We are based on the County Antrim coast in Carrickfergus, perfectly placed to see major tourist attractions such as the <b>Giants Causeway</b> and <b>Game of Thrones</b> locations.<br /><br /></ScrollAnimation>
                  <ScrollAnimation animateIn="fadeInLeft" delay={200} offset={80}>
<b>Carrickfergus Castle</b> is a striking feature of the landscape from land, sea and air, and greets all visitors with its strength and menace. It represents over 800 years of military might. Besieged in turn by the Scots, Irish, English and French, the Castle saw action right up to World War II. Today it is maintained by the Environment and Heritage Service and can be enjoyed by visitors wanting to learn more about its history or just looking for a fun day out in a unique setting.<br /><br />
<b>Carrickfergus Museum</b> Located in the heart of the town, the new Carrickfergus Museum explores centuries of history through a range of fascinating collections that are being displayed in their home town for the first time.<br /><br />
</ScrollAnimation>
<ScrollAnimation animateIn="fadeInRight" delay={300} offset={80}>
<b>The Andrew Jackson and US Rangers Cottage</b> Located on the outskirts of the town at Boneybefore, the cottage highlights the historic American connections with Carrickfergus.<br /><br />
<b>Fishing</b> in Ireland is recognized as being the outstanding angling destination in Europe. The vast variety and quality of fishing in Ireland has given the country a reputation of which we are justly proud.<br /><br /><br />
</ScrollAnimation>
		<ScrollAnimation animateIn="fadeInLeft" delay={400} offset={80}>
Whatever you do make sure you come to The Keep Guesthouse, Carrickfergus on the County Antrim Coast. Contact us now to begin your Northern Ireland holiday adventure.<br /><br />
If you have any queries about any of the sights across Northern Ireland, please do not hesitate to ask us when you come and stay, email or call us.
                  </ScrollAnimation>
                </div>
              <br />
            </div>
            <br />

        </section>
      </div>
    );
  }
}

export default Attractions;
