import React from "react";
import "./css/animate.css";
import ScrollAnimation from "./react-animate-on-scroll_/scroll-animation.js";

class Booking extends React.Component {
  render() {
    return (
      <div>
        <br />

        <section id="booking">
          <br />

          <div className="center-image">
            <ScrollAnimation
              animateIn="fadeInDown"
              animateOnce={true}
              offset={30}
            >
              <h2 className="titleText">BOOKING</h2>
            </ScrollAnimation>
            <br />

	<div className="attractionsContainer">
                  <ScrollAnimation animateIn="fadeInUp" delay={100} offset={80}>
                    Our great standard daily rate per person includes a full cooked Ulster breakfast (Dietary requirements including Vegetarian/Vegan are catered for).<br /><br />

<div className="center-image">
<span className="rate">From</span><br /><b className="rate">£30.00 Per Person Sharing</b><br />
<b className="rate">£50.00 Single Room</b><br />
</div>
                  </ScrollAnimation>

<br /><br />
 <ScrollAnimation animateIn="fadeInUp" delay={150} offset={80}>
		For booking enquiries and reservations please contact Raymond and Heather.<br /><br />
		</ScrollAnimation>
                </div>
              <br />

            </div>


<div className="center-image">
 <span className="fb-icon">
                {" "}
                <a
                  href="https://www.booking.com/hotel/gb/the-keep-guesthouse.en-gb.html"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  {" "}
                  <img src={require("./img/booking-logo.png")} alt="messenger" />
                </a>
              </span>
</div>


                
            <br />
            <br />

        </section>
      </div>
    );
  }
}

export default Booking;
